	module.exports = {"data":{"markdownRemark":{"html":"<hr>\n<p>This site uses Gatsby.js.</p>\n<p>This particular page is created with a markdown file.</p>\n<h3>Some Cool Things About Gatsby:</h3>\n<ul>\n<li>Uses GraphQL</li>\n<li>Check out <code>/my-files</code> to see a graphQL generated list of files</li>\n<li>This is possible via a gatsby source plugin called \"gatsby-source-filesystem\" that can live reload data</li>\n<li>Generates a single static file, similar to webpack, that you can post to a simple static site host, including github pages.</li>\n<li>Gatsby lets you use GraphQL to query your data and map the data to pages—all at build time.</li>\n</ul>\n<h3>Some more specifics</h3>\n<ul>\n<li>Source plugins bring data into Gatsby's data system</li>\n<li>Transformer plugins transform raw content brought by source plugins</li>\n</ul>\n<h3>Markdown Stuff</h3>\n<ul>\n<li>To create pages from markdown files, we use two Gatsby API's\n-onCreateNode\n-createPages</li>\n</ul>","frontmatter":{"title":"About This Website"}}},"pathContext":{"slug":"/about-this-site/"}}

